import fetch from "isomorphic-fetch";
import React from "react";
import moment from "moment/moment";
import _ from "lodash";
class Hospitals extends React.Component {

    render() {
        if (_.isEmpty(this.props.hospitalslist)) {
            return 'Loading';
        }
        return (
            <div>
                { this.props.hospitalslist.map( ( price ) => (
                    <div key={ price.id } >
                        <div className="row">
                            <div className="col">
                                {moment(price.sold_date,"YYYY-MM-DD").format("MMMM YYYY")}
                            </div>
                            <div className="col">
                                &pound;{price.price}
                            </div>
                        </div>


                    </div>
                ) ) }
            </div>
            );
    }
}



export default Hospitals;


